nav {
    background-color: rgb(17, 17, 17);
    color: white;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: sticky;
    top: 0;
    width: 100%;
    height: 10vh;
    z-index: 11;
}

.logo {
    z-index: 10;
    text-align: left;
    width: 80%;
    font-size: 1rem;
}

.menu-icon {
    display: flex;
    flex-direction: column;
    /* padding-left: 8px; */
}


i {
    background-color: rgb(255, 255, 255);
    width: 20px;
    height: 2px;
    margin: 2px;
    border-radius: 2px;
    z-index: 10;
    /* transition:all ease 0.5s; */
}

.nav-links-closed{
    position: absolute;
    background-color: rgb(27, 27, 27, 0.911);
    color: rgb(248, 248, 248);
    margin-top: 110vh;
    margin-left: -100vh;
    width: 100vw;
    height: 100vh;
    transition: all 0.5s ease;
    
}

.nav-links-closed ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-links-closed ul li {
    margin: 2.5rem;
    font-size: 1.2rem;
    font-weight: 500;
}

.nav-links {
    position: absolute;
    background-color: rgba(27, 27, 27, 0.911);
    color: rgb(248, 248, 248);
    margin-top: 110vh;
    width: 100vw;
    height: 100vh;
    transition: all .5s ease;

}

.nav-links ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-links ul li {
    margin: 3rem 0;
    font-size: 1.2rem;
    font-weight: 500;
}